import { Component } from "@angular/core";
import { DataServiceService } from "./data-service.service";
import { NgxSpinnerService } from "ngx-spinner";
import { FormControl, Validators, FormGroup } from "@angular/forms";
import { Router } from "@angular/router";
var alertify = require("alertifyjs");
@Component({
  selector: "app-root",
  templateUrl: "./app.component.html",
  styleUrls: ["./app.component.scss"]
})
export class AppComponent {
  currentUser: string;
  title = "Angaza Elimu";
  login_form: any;
  login_status: boolean = false;
  username: string;
  user_type;
  constructor(
    private data_service: DataServiceService,
    private spinner: NgxSpinnerService,
    private router: Router
  ) {
    this.currentUser = localStorage.getItem("user_pk");
    this.username = localStorage.getItem("angaza_user");
    this.user_type = localStorage.getItem("user_type");
    console.log(this.currentUser);
    console.log(this.username);
    this.currentUser && this.username
      ? this.goToNotes()
      : this.setFalseLoginStatus();
    if (this.currentUser && this.username && this.user_type == "student") {
      this.goToNotes();
    } else if (
      this.currentUser &&
      this.username &&
      this.user_type == "teacher"
    ) {
      this.goToTeacherDashboard();
    } else {
      this.setFalseLoginStatus();
    }

    const name = new FormControl("", Validators.required);
    const password = new FormControl("", Validators.required);

    this.login_form = new FormGroup({
      name: name,
      password: password
    });
  }
  goToNotes() {
    this.spinner.show();
    this.login_status = true;
    console.log("clicked");
    this.router.navigate(["notes/math"]);
  }
  setFalseLoginStatus() {
    this.login_status = false;
    localStorage.removeItem("user_pk");
    localStorage.removeItem("angaza_user");
  }

  loginAttempt() {
    this.spinner.show();
    this.data_service.login(this.login_form.value).subscribe(
      response => {
        console.log(this.login_form.value);
        if (response["access_token"] && response["user_type"] == "student") {
          localStorage.setItem("user_type", response["user_type"]);
          localStorage.setItem("school_code", response["school_code"]);
          localStorage.setItem("user_id", response["id"]);
          localStorage.setItem("user_pk", response["access_token"]);
          localStorage.setItem("angaza_user", response["username"]);
          localStorage.setItem('angaza_access_token', response['access_token'])
          this.username = response["username"];
          alertify.alert(
            "Login Successful",
            "You have logged in Successfully.",
            function() {
              alertify.message("OK");
            }
          );
          this.goToNotes();

          this.login_status = true;
        } else if (
          response["access_token"] &&
          response["user_type"] == "teacher"
        ) {
          localStorage.setItem("user_type", response["user_type"]);
          localStorage.setItem("user_id", response["id"]);
          localStorage.setItem("user_pk", response["access_token"]);
          localStorage.setItem("angaza_user", response["username"]);
          alertify.alert(
            "Login Successful",
            "You have logged in Successfully.",
            function() {
              alertify.message("OK");
            }
          );
          this.goToTeacherDashboard();
          this.login_status = true;
          this.username = response["username"];
        }
        console.log(response);
        this.spinner.hide();

        alertify.alert("Login Successful", "Login was successful.", function() {
          alertify.message("OK");
        });
      },
      err => {
        alertify.alert(
          "Unsuccessful Login",
          "Login Unsuccessful. Check Username and Password.",
          function() {
            alertify.message("OK");
          }
        );
        this.spinner.hide();
      }
    );
  }
  goToTeacherDashboard() {
    this.router.navigate(["/teacher_dash"]);
  }

  goToNotesPage(){
    this.spinner.show();
    console.log("clicked");
    // localStorage.setItem('subject_id', subject_id);
    this.router.navigateByUrl("notes/english");
  }
  logout() {
    if (confirm("Do you want to log out?")) {
      localStorage.removeItem("user_pk");
      localStorage.removeItem("angaza_user");
      this.username = "";
      this.login_status = false;
      this.router.navigateByUrl("/");
    } else {

    }

  }
  signupPage() {
    this.router.navigateByUrl("/sign");
  }
}
