import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators} from '@angular/forms';
import { DataServiceService } from '../data-service.service';
import { Router } from "@angular/router";
@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.scss']
})
export class SignupComponent implements OnInit {
  signupForm: any;
  constructor(private data_service: DataServiceService, private router: Router) {
    const user_type = new FormControl('', Validators.required);
    const email = new FormControl('', Validators.required);
    const name = new FormControl('', Validators.required);
    const password = new FormControl('', Validators.required);
    const school_code = new FormControl('');
    const confirm_password = new FormControl('', Validators.required);

    this.signupForm = new FormGroup({
      name: name,
      email: email,
      user_type: user_type,
      password: password,
      confirm_password: confirm_password,
      school_code: school_code,

    })
   }

  ngOnInit() {
  }

  signupAttempt(){
    console.log(this.signupForm.value);
    this.data_service.signup(this.signupForm.value).subscribe(response => {
      console.log(response);
      this.router.navigateByUrl('/home')
    })
  }

}
